

































import Vue from 'vue'

export default Vue.extend({
  props: {
    routeNames: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    const items: any[] = []
    const colorModes = [
      {
        mode: 'system',
        display: 'System'
      },
      {
        mode: 'light',
        display: 'Light'
      },
      {
        mode: 'dark',
        display: 'Dark'
      },
      {
        mode: 'sepia',
        display: 'Sepia'
      }
    ]
    return {
      colorModes,
      items
    }
  },
  watch: {
    $route () {
      this.breadcrumbItems()
    }
  },
  created () {
    this.breadcrumbItems()
  },
  methods: {
    breadcrumbItems () {
      const currName = this.$route.name!
      const curr = this.routeNames[currName]
      const items = [
        {
          text: '鲨手酒吧',
          to: '/'
        }
      ]
      items.push(curr)
      this.items = items
    },
    setColorMode (mode: string) {
      this.$colorMode.preference = mode
    },
    getColorModeDisplay (mode: string) {
      return this.colorModes.find(p => p.mode === mode)?.display
    },
    getColorMode () {
      return this.$colorMode.preference
    }
  }
})
